@import url('https://fonts.googleapis.com/css2?family=Lato&family=Poppins:wght@300;400&family=Work+Sans&display=swap');

*{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

body{
  box-sizing: border-box;
  background-image: conic-gradient(from 0deg, navy, blue, dodgerblue, navy);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Login Page */

.container{
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form-container{
  z-index: 2;
  width: fit-content;
  padding: 5rem 2rem 2rem 3rem;
  height: fit-content;
  border-radius: 1.5rem;
  background-color: rgba(205, 188, 204, 0.85);
  box-shadow: 10px 5px 20px rgb(0, 0, 255, 0.8);
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.form-container form{
  display: flex;
  flex-direction: column;
}

.form-container form > div{
  display: flex;
  align-items: center;
}

input[type="text"], input[type="password"]{
  background-color: #385273;
  height: 1rem;
  padding: 0.7rem;
  border: none;
  color: #f1f1f1;
  transition: 1s all;
}

.password-container{
  margin-top: 1rem;
  position: relative;
}

.eye-icon{
  position: absolute;
  right: 0.3rem;
  color: white;
  cursor: pointer;
}

/* input[type="checkbox"]{
  margin-bottom: -0.5rem;
} */

input::placeholder{
  color: #d3d3d3;
  letter-spacing: 0.05rem;
}

.remember_forgot{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.remember{
  position: relative;
  align-items: center;
}

.checkbox{
  position: absolute;
  top: 7px;
  cursor: pointer;
}

.checkbox:checked{
  accent-color: #00246d;
}

label{
  margin-left: 1.2rem;
  cursor: pointer;
}

.login-btn-container{
  z-index: 1;
  width: fit-content;
  padding: 0.5rem 5rem;
  background-color: rgba(205, 188, 204, 0.85);
  border-radius: 0 0 1.5rem 1.5rem;
  box-shadow: 10px 5px 20px rgb(0, 0, 255, 0.8);
}

.login-btn-container button{
  border: none;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 0.8rem;
  color: white;
  cursor: pointer;
  letter-spacing: 0.15rem;
  background-color: transparent;
}

button:disabled{
  cursor: auto;
}

.login-message{
  color: white;
  text-align: center;
  font-size: 0.6rem;
}


/* Forgot Password Section */

.fp-container{
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
  display: grid;
  justify-content: center;
  color: white;
}




/* Post / Homepage Section */

.home{
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  min-height: 100vh;
}

.posts{
  list-style-type: none;
}

.post{
  position: relative;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  width: 450px;
  margin-bottom: 1rem;
  padding: 1.5rem;
  background: #000;
  border-radius: 1.5rem;
  filter: opacity(0.85);
}

.delete-post{
  position: absolute;
  right: -1.5rem;
  top: 3rem;
  display: flex;
  align-items: center;
  width: fit-content;
  padding: 0.2rem 0.5rem;
  color: white;
  background-color: black;
  border-radius: 0.5rem;
  list-style: none;
  cursor: pointer;
}

.delete-post .delete{
  display: none;
  border: solid 1px red;
}

.post div.about-user{
  display: flex;
  height: 50px;
  align-items: flex-start;
  justify-content: space-between;
}

.user-info{
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.header{
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.header > div{
  position: relative
}

.dp{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

.header > div > span {
  position: absolute;
  bottom: 7px;
  right: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.posts-heading{
  cursor: pointer;
}

.create{
  display: flex;
  column-gap: 0.5rem;
  align-items: center;
  cursor: pointer;
}

.pfp img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.names{
  align-self: center;
}

.names p{
  color: #d3d3d3;
  margin-top: -0.5rem;
}

.post-text{
  margin-top: 1.5rem;
  font-size: 1.25rem;
  line-height: 1.5rem;
}

.date-time{
  margin-top: 1rem;
}

.options{
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.icon{
  color: #d3d3d3;
  transition: all 0.2s;
  cursor: pointer;
}

.icon:hover{
  color: white;
}

.heart{
  color: red;
}

.footer p{
  display: flex;
  align-items: center;
  justify-content: right;
  column-gap: 0.5rem;
  color: white;
  font-weight: bold;
  padding: 1rem;
}

@media screen and (max-width: 600px){
  .post{
    width: 80vw;
  }
}

/* Create post section */

.create-post{
  display: grid;
  place-items: center;
  height: 100vh;
}

.create-post-container{
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  background-color: black;
  border-radius: 1rem;
  width: 25%;
  padding: 1rem;
  overflow-x: hidden;
}

.option-btn{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.close{
  color: white;
  transition: all 0.15s;
}

.close:hover{
  color: dodgerblue;
  cursor: pointer;
}

.post_options{
  display: flex;
  column-gap: 1rem;
}

.draft{
  color: dodgerblue;
  font-weight: bold;
  background-color: transparent;
  cursor: pointer;
}

.new-post:disabled{
  background-color: gray;
  color: white;
  font-weight: bold;
  padding: 0.2rem 0.75rem;
  border-radius: 1rem;
  border: none;
}

.new-post{
  color: white;
  font-weight: bold;
  padding: 0.2rem 0.75rem;
  border-radius: 1rem;
  background-color: dodgerblue;
  cursor: pointer;
  border: none;
}

.user_pic{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.privacy_post{
  display: flex;
  column-gap: 1rem;
}

.privacy{
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}

.privacy button{
  display: flex;
  align-items: center;
  color: dodgerblue;
  font-weight: bold;
  background-color: transparent;
  border: solid 1.5px dodgerblue;
  padding: 0.2rem 0.75rem;
  border-radius: 1rem;
  cursor: pointer;
}

.privacy div{
  display: flex;
}

textarea{
  background-color: transparent;
  border: none;
  resize: none;
  color: white;
}

textarea::placeholder{
  color: #d3d3d3;
}

textarea:focus{
  outline: none;
}

.validation-output{
  color: white;
}

.images{
  display: flex;
  column-gap: 0.5rem;
  overflow-x: hidden;
}

.images > div:first-of-type{
  background-color: transparent;
  border: solid 1px gray;
}

.images > div{
  position: relative;
  overflow: hidden;
}

input[type="file"]{
  position: absolute;
  color: transparent;
  height: 50px;
  border-radius: 1rem;
  border: solid 1px red;
  cursor: pointer;
}

.images > div{
  display: grid;
  place-items: center;
  background-color: white;
  width: 50px;
  height: 50px;
  border-radius: 1rem;
}

.reply{
  display: flex;
  column-gap: 1rem;
  align-items: center;
  font-size: 0.8rem;
  color: dodgerblue;
  cursor: pointer;
}

.icon-options div{
  display: flex;
  column-gap: 1rem;
}

@media screen and (width: 1024px){
  .images > div:nth-of-type(5){
    display: none;
  }
}

@media screen and (max-width: 850px){
  .create-post-container{
    width: 40vw;
  }
}

@media screen and (max-width: 600px){
  .create-post-container{
    width: 80vw;
  }
}

@media screen and (max-width: 300px){
  .create-post-container{
    width: 90vw;
  }

  .images > div:nth-of-type(5){
    display: none;
  }
}

/* Error page */

.error{
  width: 90vw;
  height: 90vh;
  color: white;
  padding: 3rem;
}

.error h1{
  font-size: 2rem;
}